.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    border: 1px solid;
    height: 200px;
    width: 400px;
    font-size: 3rem;
    background: blanchedalmond;
    border-radius: 10px;
  
}

.modalText{
    text-align: center;
}